<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot>
      </slot>
    </div>
    <div class="dots">
      <span class="dot" :class="{active: currentPageIndex === index }" v-for="(item, index) in dots"></span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {addClass} from 'common/js/dom'
  import BScroll from 'better-scroll'

  export default {
    name: 'slider',
    props: {
      loop: {     // ÊòØÂê¶Âæ™ÁéØËΩÆÊí≠
        type: Boolean,
        default: true
      },
      autoPlay: { // ÊòØÂê¶Ëá™Âä®Êí≠Êîæ
        type: Boolean,
        default: true
      },
      interval: { // ËΩÆÊí≠Èó¥ÈöîÊó∂Èó¥
        type: Number,
        default: 4000
      }
    },
    data() {
      return {
        dots: [],   // Â∞èÂúÜÁÇπÂØºÂêë
        currentPageIndex: 0   // ÂΩìÂâçÊí≠ÊîæÁöÑ‰∏ãÊ†á
      }
    },
    mounted() {
      setTimeout(() => {
        this._setSliderWidth()      // ËÆæÁΩÆËΩÆÊí≠ÊÄªÂÆΩÂ∫¶
        this._initDots()            // ÂàùÂßãÂåñ‰∏ãÊñπÂ∞èÁÇπ
        this._initSlider()          // ÂàùÂßãÂåñËΩÆÊí≠

        if (this.autoPlay) {
          this._play()        // Ëá™Âä®Êí≠Êîæ
        }
      }, 20)

      window.addEventListener('resize', () => {   // ËßÜÂè£ÊîπÂèòËá™ÈÄÇÂ∫î
        if (!this.slider) {
          return
        }
        this._setSliderWidth(true)    // ÈáçÊñ∞ËÆæÁΩÆÂÆΩÂ∫¶
        this.slider.refresh()       // Âà∑Êñ∞ËΩÆÊí≠
      })
    },
    activated() {
      if (this.autoPlay) {
        this._play()
      }
    },
    deactivated() {
      clearTimeout(this.timer)
    },
    beforeDestroy() { // Âú®ÈîÄÊØÅ‰πãÂâçÊ∏ÖÈô§ËÆ°Êó∂Âô®
      clearTimeout(this.timer)
    },
    methods: {
        // ËÆæÁΩÆËΩÆÊí≠ÂÆΩÂ∫¶
      _setSliderWidth(isResize) {
        this.children = this.$refs.sliderGroup.children   // Áõ¥Êé•Â≠êËäÇÁÇπÔºåËΩÆÊí≠ÂõæÁöÑ‰∏™Êï∞
        let width = 0
        let sliderWidth = this.$refs.slider.clientWidth   // ËßÜÂè£Â§ßÂ∞è
        for (let i = 0; i < this.children.length; i++) {
          let child = this.children[i]
          addClass(child, 'slider-item')      // Ê∑ªÂä†Ê†∑Âºè

          child.style.width = sliderWidth + 'px'  // ËÆæÁΩÆÂÆΩÂ∫¶
          width += sliderWidth    // ËΩÆÊí≠ÊÄªÂÆΩÂ∫¶ÔºàÂÆû‰æã‰∏≠ÊòØ5ÔºåÂ∞±ÊòØ5ÂÄçÂ§ßÂ∞èÔºâ
        }
        if (this.loop && !isResize) {
          width += 2 * sliderWidth      // Â¶ÇÊûúÊòØÂæ™ÁéØÊí≠ÊîæÔºåÂú®È¶ñÂ∞æÂêÑÂ§ö‰∏Ä‰∏™ÔºåÊâÄ‰ª•ÊòØÊú¨‰æã‰∏≠ÊòØÔºà5+2ÔºâÂÄçÂ§ßÂ∞è
        }
        this.$refs.sliderGroup.style.width = width + 'px'  // ËÆæÁΩÆËΩÆÊí≠ÊÄªÂÆΩÂ∫¶
      },
      _initSlider() {
        this.slider = new BScroll(this.$refs.slider, {
          scrollX: true,      // Ê®™ÂêëÊªöÂä®
          scrollY: false,
          momentum: false,
          snap: true,
          snapLoop: this.loop,
          snapThreshold: 0.3,
          snapSpeed: 400
        })

        this.slider.on('scrollEnd', () => {
          let pageIndex = this.slider.getCurrentPage().pageX  // ÂΩìÂâçËΩÆÊí≠Â∫èÂè∑ÔºåÂ∫îËØ•ÂØπÈ¶ñÂ∞æËøõË°å‰∫ÜÂ§ÑÁêÜ
          if (this.loop) {
            pageIndex -= 1
          }
          this.currentPageIndex = pageIndex   // ËÆæÁΩÆÂ∞èÁÇπÊøÄÊ¥ªÁä∂ÊÄÅ

          if (this.autoPlay) {    // ÁªßÁª≠ËΩÆÊí≠
            this._play()
          }
        })

        this.slider.on('beforeScrollStart', () => {   // Ëß£ÂÜ≥Ëá™Âä®Êí≠Êîæ‰∏éÊâãÂä®ÊªëÂä®ÁöÑÂÜ≤Á™Å
          if (this.autoPlay) {
            clearTimeout(this.timer)
          }
        })
      },
      _initDots() {
        this.dots = new Array(this.children.length)  // Â∞±ÊòØ‰∏Ä‰∏™ÁÆÄÂçïÁöÑÊï∞ÁªÑ
      },
      _play() {       // ËΩÆÊí≠
        let pageIndex = this.currentPageIndex + 1
        if (this.loop) {    // Âæ™ÁéØËΩÆÊí≠ÊòØ+2
          pageIndex += 1
        }
        this.timer = setTimeout(() => {
          this.slider.goToPage(pageIndex, 0, 400)  // Áõ¥Êé•ÂéªÂà∞‰∏ã‰∏ÄÈ°µ
        }, this.interval)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slider
    min-height: 1px
    .slider-group
      position: relative
      overflow: hidden
      white-space: nowrap
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      bottom: 12px
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        &.active
          width: 20px
          border-radius: 5px
          background: $color-text-ll
</style>



